#welcome {
    padding-top: 38px;
    font-family: 'Oswald', sans-serif;
    font-size: 107px;
    float: left;
    font-weight: bolder;
}
#projects {
    padding-top: 38px;
    font-family: 'Oswald', sans-serif;
    font-size: 100px;
    margin-bottom: 50px;
    font-weight: bolder;
}
#intro,#aboutMe,#career,#contact {
    padding-top: 35px;
    font-family: 'Oswald', sans-serif;
    font-size: 65px;
}
#bio {
    color: black;
}
p {
    font-size: 25px;
    font-family: 'Oswald', sans-serif;

}
hr {
    border: 4px solid rgb(56,56,56);
  border-radius: 4px;
}
.thumbnailImage {
    max-width: 150px;
    height: auto;
    float: right;
    margin-top: 38px;
    margin-left: 10px;
    margin-right: auto;

}
.rowOne, .rowTwo {
    background-color: bisque;
    margin-left: auto;
    margin-right: auto;
    border-radius: 7px;
}

/* Media Query Mobile Devices */
@media screen and (max-width: 480px) {
    #welcome {
        padding-top: 35px;
        font-size: 85px;
    }
    p {
        font-size: 19px;
    }
    #intro {
        padding-top: 6px;
    }
    #projects {
        padding-top: 38px;
        font-size: 70px;
    }

  }